<template>
<div class="map">
  <div class="shell">
    <div class="box" :class="{'active':isActive}" @click="change(1)"><i class="el-icon-view"></i><span>地域</span></div>
    <div class="box"  :class="{'active':!isActive}" @click="change(2)"><i class="el-icon-s-grid"></i><span>地貌</span></div>
  </div>
  <component :is="codeName"></component>
</div>
</template>

<script>
import Map from '@/views/Map'
import Map2 from '@/views/Map2'
export default {
  name: 'BTn',
  data () {
    return {
      codeName: Map,
      isActive: true
    }
  },
  methods: {
    change (num) {
      if (num === 1) {
        this.codeName = Map
        this.isActive = true
      } else {
        this.codeName = Map2
        this.isActive = false
      }
    }
  }
}
</script>

<style scoped lang="scss">
.map{
  height: 100vh;
}
.shell{
  width: 100px;
  height: 100px;
  position: absolute;
  left: 8%;
  top: 12.5%;
  display: flex;
  justify-content: center;
  flex-direction: column;
  background-color: #ededed;
  align-items: center;
  border-radius: 10px;
  transition: .3s;
  overflow: hidden;
  z-index: 999;
}
.box{
  cursor: pointer;
  padding-top: 8px;
  padding-left: 8px;
  box-sizing: border-box;
  height: 45%;
  width: 85%;
  margin: 8px;
  border-radius: 5px;
  position: relative;
  transition: .3s;
  color: gray;
  i{
   width: 16px;
  }
  span{
    margin-left: 8px;
  }
}
.box:hover{
  background-color: #86b8a3;
  color: whitesmoke;
}
.active{
  background-color: #429070;
  color: whitesmoke;
}

</style>
