<template>
  <div>
    <div class="logobox">
      <img src="../../assets/img/logoUd.png" class='logo'>
    </div>
    <form action="#" class="form" id="form1">
      <div class="form_title">
        <span class="title_up">注册</span>
        <span class="title_in" @click="moveToSignIn">/登录</span>
      </div>
      <input type="username" placeholder="用户名" class="input" v-model="person.username" @focus="clear"/>
      <input type="email" placeholder="邮箱/手机号" class="input" v-model="person.email" @focus="clear"/>
      <div class="code"><input type="text" placeholder="验证码" class="input" v-model="person.code" @focus="clear"/>
        <span v-show="inCode" @click="sendCode">{{btn_text}}</span>
        <span v-show="!inCode">{{code_time}}(重新发送)</span>
      </div>
      <input type="password" placeholder="密码" class="input" v-model="person.password" maxlength="15" @focus="clear"/>
      <span class="warn">{{warn}}</span>
      <button class="btn" @click.prevent="sendSignUp">注册</button>
    </form>
  </div>
</template>

<script>
import { postSignUp, postCode, postSignIn } from '@/API'
import qs from 'qs'
export default {
  name: 'MobileReg',
  data () {
    return {
      isActive: true,
      person: {
        username: '',
        password: '',
        email: '',
        code: ''
      },
      user: {
        username: '',
        password: ''
      },
      inCode: true,
      code_time: 0,
      btn_text: '获取验证码',
      warn: ''
    }
  },
  methods: {
    changeSignIn () {
      this.isActive = false
    },
    changeSignUp () {
      this.isActive = true
    },
    sendSignUp: function () {
      console.log(this.person)
      postSignUp(qs.stringify(this.person)).then((data) => {
        console.log(data)
        if (data.state !== 'success') {
          this.warn = data.messages
        } else {
          this.$store.state.loginState = true
          this.$router.push('/Main')
        }
      })
        .catch(function (err) {
          console.log(err)
        })
    },
    sendCode: function () {
      const reg = /^([a-zA-Z]|[0-9])(\w)+@[a-zA-Z0-9]+\.([a-zA-Z]{2,4})$/
      if (reg.test(this.person.email)) {
        this.warn = ''
        postCode(qs.stringify({ email: this.person.email })).then((data) => {
          // if (data.state === 'emailError') {
          //   const msg = data.messages
          //   this.form[2].Warn = msg
          // } else {
          console.log(data)
          this.inCode = false
          this.code_time = 30
          this.timer()
        })
          .catch(function (err) {
            console.log(err)
          })
      } else {
        this.warn = '邮箱格式不正确'
      }
      console.log(this.person.email)
    },
    sendSignIn: function () {
      postSignIn(qs.stringify(this.user)).then((data) => {
        console.log(data)
        if (data.state !== 'success') {
          this.warn = data.messages
        } else {
          this.$store.state.loginState = true
          this.$router.push('/Main')
        }
      })
        .catch(function (err) {
          console.log(err)
        })
    },
    timer: function () {
      if (this.code_time > 0) {
        this.code_time--
        this.btn_text = `${this.code_time}s后重新发送`
        setTimeout(this.timer, 1000)
      } else {
        this.code_time = 0
        this.btn_text = '获取验证码'
        this.inCode = true
      }
    },
    moveToRepass () {
      this.$router.push('')
    },
    moveToSignIn () {
      this.$router.push('/User2/PCLog')
    },
    clear () {
      this.warn = ''
    }
  }
}
</script>

<style scoped lang="scss">

.form {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  height: 100%;
  text-align: center;
  margin-left: 7.03rem;
  margin-right: 7.03rem;
  padding-top:0.4rem;
  padding-bottom:0.34rem;
  border-radius: 0.2rem;
  background-color: rgba($color:#ffffff, $alpha: 0.5);
}

.logobox {
  text-align: center;
  .logo{
    height: 1.18rem;
    width: 1.22rem;
  }
  margin-bottom: 0.21rem;
}

.code{
  position: relative;
  span{
    display: block;
    text-align: center;
    right: 0.1rem;
    top: 0.07rem;
    font-family: 'jiangxizhuokai';
    box-sizing: border-box;
    //padding: 0.1rem,0.17rem,0.0967rem,0.17rem;
    position: absolute;
    //text-align: center;
    font-size: 0.16rem;
    color: #1a1a1a;
    width: 1.2rem;
    height: 0.46rem;
    background: #FFCA29;
    border-radius: 0.16rem;
    line-height: 0.48rem;
  }
}
.form_title {
  font-family: 'jiangxizhuokai';
  display: inline-block;
  margin-bottom: 0.55rem;
  .title_up{
    color: #000000;
    font-size: 0.48rem;
    margin-right: 0.1rem;
    cursor: pointer;
  }
  .title_in{
    color:#666666;
    font-size: 0.38rem;
    cursor: pointer;
  }
}

.input {
  font-size: 0.22rem;
  box-sizing: border-box;
  background-color: rgba($color: #ffffff, $alpha: 0.85);
  border: none;
  padding-left: 0.28rem;
  border-radius: 0.1rem;
  margin-bottom: 0.15rem;
  width: 3.5rem;
  height: 0.6rem;
  outline: none;
  font-weight: bolder;
}

// placeholder属性设置
::-webkit-input-placeholder { color:#4D4D4D;  font-family: 'jiangxizhuokai'; font-weight: initial; font-size: 0.18rem;}
::-moz-placeholder { color:#4D4D4D; font-family: 'jiangxizhuokai'; font-weight: initial; font-size: 0.18rem;} /* firefox 19+ */
:-ms-input-placeholder { color:#4D4D4D; font-family: 'jiangxizhuokai'; font-weight: initial; font-size: 0.18rem;} /* ie */
input:-moz-placeholder { color:#4D4D4D; font-family: 'jiangxizhuokai'; font-weight: initial; font-size: 0.18rem;}

.btn {
  margin-top: 0.88rem;
  background-color: #FFCA29;
  font-family: 'jiangxizhuokai';
  border: none;
  width: 2.15rem;
  height: 0.75rem;
  cursor: pointer;
  font-size: 0.3rem;
  text-align: center;
  border-radius: 0.38rem;
  color: #1A1A1A;
}
.warn{
  color: #ff0000;
  font-size: 0.20rem;
  font-weight: 1000;
}
</style>
