<template>
<div class="reBox">
  <div class="RePassBox">
    <h2 class="form__title">修改密码</h2>
    <input type="text" placeholder="用户(已注册用户)" class="input"  v-model="person.username" @focus="clear"/>
    <input type="email" placeholder="邮箱" class="input" v-model="person.email" @focus="clear"/>
    <div class="code"><input type="text" placeholder="验证码" class="input" v-model="person.code" @click="clear"/>
      <span v-show="inCode"  @click="sendCode" @focus="clear">{{btn_text}}</span>
      <span v-show="!inCode">{{code_time}}(重新发送)</span>
    </div>
    <input type="password" placeholder="新密码" class="input" v-model="person.new_password" @focus="clear"/>
    <span class="warn">{{warn}}</span>
    <button class="btn" @click="sendRePass">确定</button>
  </div>
</div>
</template>

<script>
import { postRePass, postCode } from '../../API'
import qs from 'qs'

export default {
  name: 'RePass',
  data () {
    return {
      person: {
        username: '',
        new_password: '',
        email: '',
        code: ''
      },
      inCode: true,
      code_time: 0,
      btn_text: '获取验证码',
      warn: ''
    }
  },
  methods: {
    sendCode: function () {
      const reg = /^([a-zA-Z]|[0-9])(\w)+@[a-zA-Z0-9]+\.([a-zA-Z]{2,4})$/
      if (reg.test(this.person.email)) {
        this.warn = ''
        postCode(qs.stringify({ email: this.person.email })).then((data) => {
          // if (data.state === 'emailError') {
          //   const msg = data.messages
          //   this.form[2].Warn = msg
          // } else {
          console.log(data)
          this.inCode = false
          this.code_time = 30
          this.timer()
        })
          .catch(function (err) {
            console.log(err)
          })
      } else {
        this.warn = '邮箱格式不正确'
      }
      console.log(this.person.email)
    },
    sendRePass: function () {
      console.log(this.person)
      postRePass(qs.stringify(this.person)).then((data) => {
        console.log(data)
        if (data.state !== 'success') {
          this.warn = data.messages
        } else {
          this.$router.push('/Main')
        }
      })
        .catch(function (err) {
          console.log(err)
        })
    },
    clear () {
      this.warn = ''
    }
  }
}
</script>

