<template>
    <div id='china_map' style='width:3.2rem;height:2.4rem;'   ref='ChinaMap'> </div>
</template>
<script>
import 'echarts/map/js/china-regions'
const echarts = require('echarts')
export default {
  name: 'MobileMap',
  mounted () {
    const _this = this
    this.$nextTick(() => {
      _this.chinachart = echarts.init(document.getElementById('china_map'))
      _this.chinachart.on('click', function (param) {
        console.log(param)
        console.log(_this.$store.state.region)
        // if (!_this.$store.state.loginState) {
        //   _this.$router.push('/MobileUser')
        // } else {
        if (param.name === '\xa0\xa0\xa0\xa0\xa0\xa0\xa0\xa0' + '华东地区') { // \xa0是因为在echart的map中通过这些字符在地图中换行，此处判断也要随便改变
          _this.$store.state.region = '山东'
          _this.$router.push('MobileRegionDetail')
        }
        if (param.name === '西南地区') {
          _this.$store.state.region = '四川'
          _this.$router.push('MobileRegionDetail')
        }
        if (param.name === '华北地区') {
          _this.$store.state.region = '北京'
          _this.$router.push('MobileRegionDetail')
        }
        if (param.name === '西北地区') {
          _this.$store.state.region = '宁夏'
          _this.$router.push('MobileRegionDetail')
        }
        if (param.name === '西南地区') {
          _this.$store.state.region = '四川'
          _this.$router.push('MobileRegionDetail')
        }
        if (param.name === '东北地区') {
          _this.$store.state.region = '辽宁'
          _this.$router.push('MobileRegionDetail')
        }
        if (param.name === '华中\n地区') {
          _this.$store.state.region = '湖北'
          _this.$router.push('MobileRegionDetail')
        }
        if (param.name === '华南地区') {
          _this.$store.state.region = '广东'
          _this.$router.push('MobileRegionDetail')
        }
        console.log(_this.$store.state.region)
        // }
        // console.log(param)
      })
      // 初始化echarts实例
      // 进行相关配置
      _this.chartOption = {
      //   title: [
      //     {
      // textStyle: {
      //   color: '#111',
      //   fontSize: 30
      // },
      // subtext: '',
      // text: '中国七大区',
      // top: 'auto',
      // subtextStyle: {
      //   color: '#aaa',
      //   fontSize: 500
      // },
      // left: 'auto'
      //     }
      //   ],
      // legend: [
      //     {
      // selectedMode: 'multiple',
      // top: 'top',
      // orient: 'horizontal',
      // data: [
      //   ''
      // ],
      // left: 'center',
      // show: true
      //     }
      //   ],
      //   backgroundColor: '#fff',
      //   series: [
      //     {
      // mapType: '中国七大区',
      // data: [],
      // name: '',
      // symbol: 'circle',
      // type: 'map',
      // roam: false
      //     }
      // ]
        geo: { // 地理坐标系组件用于地图的绘制
          map: '中国七大区', // 表示中国地图
          // roam: true, // 是否开启鼠标缩放和平移漫游
          zoom: 1.2, // 当前视角的缩放比例（地图的放大比例）
          label: {
            show: true,
            fontSize: 16
          },
          itemStyle: { // 地图区域的多边形 图形样式。
            borderColor: 'rgba(0, 0, 0, 0.2)',
            emphasis: { // 高亮状态下的多边形和标签样式
              areaColor: 'green',
              shadowBlur: 20,
              shadowColor: 'rgba(0, 0, 0, 0.5)'
            }
          }
        }
      }
      // 使用刚指定的配置项和数据显示地图数据
      _this.chinachart.setOption(this.chartOption)
    })
  },
  methods: {
    show () {
      this.chinachart.on('click', function (params) {
        console.log(params)
      })
    }
  },
  data () {
    return {
      value1: true,
      value2: true
    }
  }

}
</script>

<style scoped lang='scss'>
#china_map{
  margin-top: 0.8rem;
  width:3.3867rem;
  height:2.80rem;
}

</style>
