<template>
  <div class="bg">
<!--    <Sign></Sign>-->
    <!-- <div class="Intro">
        <h2 class="heading">中国地质公园（景区）自然地貌景观的前生今世</h2>
    </div> -->
    <div>
        <form action="#" class="form" id="form2">
          <div class="form_title">
            <span class="title_in">登录</span>
            <span class="title_up" @click="moveToSignUp">/注册</span>
          </div>
          <input type="text" placeholder="邮箱/手机号" class="input" v-model="user.auth" @focus="clear"/>
          <input type="password" placeholder="密码" class="input" v-model="user.password" @focus="clear"/>
          <span href="#" class="link" @click="moveToRepass">忘记密码了?</span>
          <span class="warn">{{warn}}</span>
          <button class="btn" @click.prevent="sendSignIn"><span class="btntext">登录</span></button>
        </form>
    </div>
  </div>
</template>

<script>
import { postSignIn } from '../../API/index'
import qs from 'qs'
export default {
  name: 'MobileLog',
  data () {
    return {
      user: {
        auth: '',
        // username: '',
        phone: '',
        email: '',
        password: ''
      },
      warn: ''
    }
  },
  methods: {
    sendSignIn: function () {
      // if (this.user.username === 'ouc' && this.user.password === 'ouc') {
      //   this.$store.state.loginState = true
      //   this.$store.state.user.username = this.user.username
      //   this.$router.push('/MobileMain')
      // } else
      if (this.user.auth === '') {
        this.warn = '邮箱或手机号为空'
      } else if (this.user.password === '') {
        this.warn = '密码为空'
      } else {
        postSignIn(qs.stringify(this.user)).then((data) => {
          console.log(data)
          if (data.state !== 'success') {
            this.warn = data.messages
          } else {
            this.$store.state.user.username = this.user.username
            this.$store.state.user.phone = this.user.phone
            this.$store.state.user.email = this.user.email
            this.$store.state.loginState = true
            this.$router.push('/MobileMain')
          }
        })
          .catch(function (err) {
            console.log(err)
          })
      }
    },
    moveToRepass () {
      this.$router.push('/MobileUser/MobileRepass')
    },
    moveToSignUp () {
      this.$router.push('/MobileUser/MobileReg')
    },
    clear () {
      this.warn = ''
    }
  }
}
</script>

<style scoped lang="scss">
.bg{
  height: 100%;
  width: 100%;
  background-size: 100% 100%;
  position: fixed;//固定定位
  background-image: url("../../assets/img/mobile/mobileLogBg.jpg");
  background-repeat: no-repeat;
  padding-top: 1.0192rem;
  // .Intro{
  //   text-align: center;
  //   .heading{
  //     font-family: 'simhei';
  //     font-size: 0.1226rem;
  //     color:#E6E6E6 ;
  //   }
  //   margin: 0 auto ;
  //   margin-bottom: 0.4033rem;
  //   overflow: hidden;
  // }
}
.form_title {
  font-family: 'jiangxizhuokai';
  display: inline-block;
  margin-bottom: 0.5267rem;
  .title_in{
    color: #FDB129;
    font-size: 0.2533rem;
    margin-right: 0.03rem;
  }
  .title_up{
    color:#CCCCCC;
    font-size: 0.1867rem;
  }
}

.input {
  font-size: 0.18rem;
  box-sizing: border-box;
  background-color: rgba($color: #fff, $alpha: 0.85);
  border: none;
  padding-left: 0.1667rem;
  border-radius: 0.0333rem;
  margin-bottom: 0.2967rem;
  width: 2.5733rem;
  height: 0.4567rem;
  outline: none;
}
.form {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  height: 100%;
  text-align: center;
}
.link {
  font-family: 'jiangxizhuokai';
  margin-top: 0.5966rem;
  margin-bottom: 0.2933rem;
  color: #D9D9D9;
  font-size: 0.12rem;
  text-decoration: none;
  cursor: pointer;
}
.btn {
  background-color: #0477C0;
  font-family: 'jiangxizhuokai';
  border: none;
  width: 1.3267rem;
  height: 0.3933rem;
  cursor: pointer;
  font-size: 0.16rem;
  text-align: center;
  border-radius: 0.1333rem;
  //opacity:0.3;
  color: #E6E6E6;
  box-shadow: 0.03rem,0.03rem,0.0967rem,#040404;
}
.warn{
  color: #ff0000;
  font-size: 0.20rem;
  font-weight: 1000;
}
// placeholder属性设置
::-webkit-input-placeholder { color:#4D4D4D; font-family: 'jiangxizhuokai'; font-weight: initial; font-size: 0.13rem;}
::-moz-placeholder { color:#4D4D4D; font-family: 'jiangxizhuokai'; } /* firefox 19+ */
:-ms-input-placeholder { color:#4D4D4D; font-family: 'jiangxizhuokai';} /* ie */
input:-moz-placeholder { color:#4D4D4D; font-family: 'jiangxizhuokai';}
</style>
