<template>
<div>
  <div class="NavBottom bg">
    <img src="../../assets/img/mobilemenu.jpg" @click="showDetail" alt="" class="leftLogo">
    <img src="../../assets/img/logoUd.png" @click="openPage" alt="" class="midLogo">
    <img :src="P1" alt="" @click="remove" class="rightLogo" v-show="!this.$store.state.loginState">
    <img :src="P2" alt="" @click="remove" class="rightLogo" v-show="this.$store.state.loginState">
  </div>
  <div class="NavDetail bg" v-show='this.$store.state.menuState'>
    <router-link tag="div" class="item" to="/MobileMain/MobilePics">
      <span>首页</span>
    </router-link>
    <router-link tag="div" class="item" to="/MobileMain/MobileIntro">
      <span>导言</span>
    </router-link>
    <router-link tag="div" class="item" to="/MobileMain/MobileMyEarth">
      <span>我的地球</span>
    </router-link>
    <router-link tag="div" class="item" to="/MobileMain/MobileMap">
      <span>奇妙大自然</span>
    </router-link>
    <router-link tag="div" class="item" to="/MobileMain/MobileStone">
      <span>地质名词索引</span>
    </router-link>
  </div>
</div>
</template>

<script>
import unlogin from '../../assets/img/unlogin.png'
import login from '../../assets/img/login.png'
export default {
  name: 'MobileMenu',
  data () {
    return {
      P1: unlogin,
      P2: login,
      state: this.$store.state.loginState
    }
  },
  methods: {
    remove () {
      if (!this.$store.state.loginState) { this.$router.push('/MobileUser') } else { this.$router.push('/MobileUser/MobileLogOut') }
    },
    showDetail () {
      if (this.$store.state.menuState) { this.$store.state.menuState = false } else { this.$store.state.menuState = true }
    },
    openPage () {
      this.$router.push('/MOpen')
    }
  }
}
</script>

