<template>
<div class="map">
  <div class="shell">
    <div class="box" :class="{'active':isActive}" @click="change(1)"><i class="el-icon-view"></i><span>地域</span></div>
    <div class="box" :class="{'active':!isActive}" @click="change(2)"><i class="el-icon-s-grid"></i><span>地貌</span></div>
  </div>
  <component :is="codeName"></component>
</div>
</template>

<script>
import Map from '@/views/Mobile/MobileMap'
import Map2 from '@/views/Mobile/MobileMap2'
export default {
  name: 'MobileBtn',
  data () {
    return {
      codeName: Map,
      isActive: true
    }
  },
  methods: {
    change (num) {
      if (num === 1) {
        this.codeName = Map
        this.isActive = true
      } else {
        this.codeName = Map2
        this.isActive = false
      }
    }
  }
}
</script>

<style scoped lang="scss">
// .map{
//   height: 100vh;
// }
.shell{
  width: 0.7rem;
  height: 0.7rem;
  position: fixed;
  top: 1rem;
  left: 5%;
  display: flex;
  justify-content: center;
  flex-direction: column;
  background-color: #ededed;
  align-items: center;
  border-radius: 0.1rem;
  transition: .3s;
  overflow: hidden;
  z-index: 999;
}
// .maparea{
//   margin-top:
// }
.box{
  cursor: pointer;
  padding-top: 0.04rem;
  padding-left: 0.04rem;
  box-sizing: border-box;
  height: 45%;
  width: 85%;
  margin: 0.01rem;
  border-radius: 0.05rem;
  position: relative;
  transition: .3s;
  color: gray;
  i{
   font-size: 0.14rem;
  }
  span{
    font-size:0.14rem;
    margin-left: 0.08rem;
  }
}
.box:hover{
  background-color: #86b8a3;
  color: whitesmoke;
}
.active{
  background-color: #429070;
  color: whitesmoke;
}
</style>
