<template>
<div class="Intro">
    <div class="herder">
        <div class="TitleBox">
            <span></span>
            <h2>{{ title }}</h2>
            <span></span>
        </div>
    </div>
    <div class="leftBoxIn">
    <mavon-editor
        class="md"
        :value="content"
        :subfield="prop.subfield"
        :defaultOpen="prop.defaultOpen"
        :toolbarsFlag="prop.toolbarsFlag"
        :editable="prop.editable"
        :scrollStyle="prop.scrollStyle"
        :toolbars="markdownOption"
    />
    </div>
</div>
</template>

<script>
import { getInfo } from '@/API'
import { mavonEditor } from 'mavon-editor'
import 'mavon-editor/dist/css/index.css'
export default {
  name: 'MobileDetail',
  components: {
    mavonEditor
  },
  data () {
    return {
      title: '',
      content: '',
      imgData: [],
      move: 0,
      markdownOption: {
        fontSize: '20px',
        lineHeight: '30px',
        toolbars: {
          imagelink: true
        }
      }
    }
  },
  created () {
    getInfo({ id: this.$route.params.id })
      .then(res => {
        console.log(res)
        this.title = res.data.title
        this.content = res.data.content
        this.imgData = res.data.pictures
        console.log(this.imgData)
      })
  },
  methods: {
    changeMarginDown () {
      if (this.move < 5) { this.move++ }
    },
    changeMarginUp () {
      if (this.move > 0) { this.move-- }
    }
  },
  computed: {
    // 解析器配置
    prop () {
      const res = {
        subfield: false, // 单双栏模式
        defaultOpen: 'preview', // edit： 默认展示编辑区域 ， preview： 默认展示预览区域
        editable: false, // 是否允许编辑
        toolbarsFlag: false,
        scrollStyle: true
      }
      return res
    }
  }
}
</script>

<style scoped lang="scss">
.Intro{
  margin: 10px 13.33px;
  width: vw;
  background: #FFFFFF;
  box-shadow: 0px 1px 32px 0px rgba(96, 96, 96, 0.25);
}
