//     line-height: 0.25rem;
//     text-indent: 0.20rem;
//   }
// }
.herder{
  padding: 0.1333rem auto;
  box-sizing: border-box;
  border-bottom: 1px solid  #A7A7A7;
}
.TitleBox{
  width:3rem;
  margin: 0 auto;
  display: flex;
  justify-content: space-between;
  align-items: center;
  height: 0.7333rem;
  span{
    display: inline-block;
    width: 0.19rem;
    height: 0.0267rem;
    background: #429070;
  }
  h2{
    font-weight: 800;
    //width: 160px;
    font-size: 0.15rem;
    text-align: center;
  }
}
</style>
