<template>
    <div class="container" :class="{rightPanelActive: isActive}">
      <!-- Sign Up -->
      <div class="container__form container--signup">
        <form action="#" class="form" id="form1">
          <h2 class="form__title">注册</h2>
          <input type="text" placeholder="用户:字母开头6-15位" class="input"  v-model="person.username" maxlength="15" @focus="clear"/>
          <input type="email" placeholder="邮箱" class="input" v-model="person.email" @focus="clear"/>
          <div class="code"><input type="text" placeholder="验证码" class="input" v-model="person.code" @focus="clear"/>
            <span v-show="inCode" @click="sendCode">{{btn_text}}</span>
            <span v-show="!inCode">{{code_time}}(重新发送)</span>
          </div>
          <input type="password" placeholder="密码:字母开头6-15位(字母，数字，下划线)" class="input" v-model="person.password" maxlength="15" @focus="clear"/>
          <span class="warn">{{warn}}</span>
          <button class="btn" @click.prevent="sendSignUp">注册</button>
        </form>
      </div>

      <!-- Sign In -->
      <div class="container__form container--signin">
        <form action="#" class="form" id="form2">
          <h2 class="form__title">登录</h2>
          <input type="text" placeholder="用户名" class="input" v-model="user.username" @focus="clear"/>
          <input type="password" placeholder="密码" class="input" v-model="user.password" @focus="clear"/>
          <span href="#" class="link" @click="moveToRepass">忘记密码了?</span>
          <span class="warn">{{warn}}</span>
          <button class="btn" @click.prevent="sendSignIn">登录</button>
        </form>
      </div>

      <!-- Overlay -->
      <div class="container__overlay">
        <div class="overlay">
          <div class="overlay__panel overlay--left">
            <button class="btn" id="signIn" @click="changeSignIn">登录</button>
          </div>
          <div class="overlay__panel overlay--right">
            <button class="btn" id="signUp" @click="changeSignUp">注册</button>
          </div>
        </div>
      </div>
    </div>
</template>

<script>
import { postSignUp, postCode, postSignIn } from '@/API'
import qs from 'qs'
export default {
  name: 'Sign',
  data () {
    return {
      isActive: true,
      person: {
        username: '',
        password: '',
        email: '',
        code: ''
      },
      user: {
        username: '',
        password: ''
      },
      inCode: true,
      code_time: 0,
      btn_text: '获取验证码',
      warn: ''
    }
  },
  methods: {
    changeSignIn () {
      this.isActive = false
    },
    changeSignUp () {
      this.isActive = true
    },
    sendSignUp: function () {
      console.log(this.person)
      postSignUp(qs.stringify(this.person)).then((data) => {
        console.log(data)
        if (data.state !== 'success') {
          this.warn = data.messages
        } else {
          this.$store.state.loginState = true
          this.$router.push('/Main')
        }
      })
        .catch(function (err) {
          console.log(err)
        })
    },
    sendCode: function () {
      const reg = /^([a-zA-Z]|[0-9])(\w)+@[a-zA-Z0-9]+\.([a-zA-Z]{2,4})$/
      if (reg.test(this.person.email)) {
        this.warn = ''
        postCode(qs.stringify({ email: this.person.email })).then((data) => {
          // if (data.state === 'emailError') {
          //   const msg = data.messages
          //   this.form[2].Warn = msg
          // } else {
          console.log(data)
          this.inCode = false
          this.code_time = 30
          this.timer()
        })
          .catch(function (err) {
            console.log(err)
          })
      } else {
        this.warn = '邮箱格式不正确'
      }
      console.log(this.person.email)
    },
    sendSignIn: function () {
      postSignIn(qs.stringify(this.user)).then((data) => {
        console.log(data)
        if (data.state !== 'success') {
          this.warn = data.messages
        } else {
          this.$store.state.loginState = true
          this.$router.push('/Main')
        }
      })
        .catch(function (err) {
          console.log(err)
        })
    },
    timer: function () {
      if (this.code_time > 0) {
        this.code_time--
        this.btn_text = `${this.code_time}s后重新发送`
        setTimeout(this.timer, 1000)
      } else {
        this.code_time = 0
        this.btn_text = '获取验证码'
        this.inCode = true
      }
    },
    moveToRepass () {
      this.$router.push('/User/Repass')
    },
    clear () {
      this.warn = ''
    }
  }
}
</script>

