import Vue from 'vue'
import VueRouter from 'vue-router'
import Main from '../views/Main'
import PIcs from '../views/PIcs'
import Instruction from '../views/Instruction'
import MyEarth from '../views/MyEarth'
import BTn from '@/views/BTn'
import Stones from '../views/Stones'
import RegionDetail from '../views/RegionDetail'
import StoneDetail from '../views/StoneDetail'
import Log from '../views/Log'
import Log2 from '../views/Log2'
import Sign from '../components/LogPiece/Sign'
import RePass from '../components/LogPiece/RePass'
import SignOut from '../components/LogPiece/SignOut'
import PCLog from '../components/LogPiece/PCLog'
import PCReg from '../components/LogPiece/PCReg'
import PCRePass from '../components/LogPiece/PCRePass'
import Detail from '../views/Detail'
import SightDetail from '@/views/SightDetail'
import Open from '@/views/Open'
import MvPlayer from '@/views/MvPlayer'
import MOpen from '@/views/Mobile/MOpen'
import MobileMain from '@/views/Mobile/MobileMain'
import MobilePics from '@/views/Mobile/MobilePics'
import MobileIntro from '@/views/Mobile/MobileIntro'
import MobileMyEarth from '@/views/Mobile/MobileMyEarth'
import MobileStone from '@/views/Mobile/MobileStone'
import MobileBtn from '@/views/Mobile/MobileBtn'
import MobileLog from '@/views/Mobile/MobileLog'
import MobileLogOut from '@/views/Mobile/MobileLogOut'
import MobileReg from '@/views/Mobile/MobileReg'
import MobileRepass from '@/views/Mobile/MobileRePass'
import MobileDetail from '@/views/Mobile/MobileDetail'
import MobileRegionDetail from '@/views/Mobile/MobileRegionDetail'
import MobileSightDetail from '@/views/Mobile/MobileSightDetail'
import MobileMineralDetail from '@/views/Mobile/MobileMineralDetail'
import MobileStoneDetail from '@/views/Mobile/MobileStoneDetail'
import MobileProcessDetail from '@/views/Mobile/MobileProcessDetail'
import MobileAgeDetail from '@/views/Mobile/MobileAgeDetail'
import MobileMvPlayer from '@/views/Mobile/MobileMvPlayer'
// import { resolve } from 'core-js/fn/promise'
Vue.use(VueRouter)
const routes = [
  {
    path: '/'
  },
  { // 路由到PC
    path: '/Open',
    component: Open
  },
  { // 路由到Mobile
    path: '/MOpen',
    component: MOpen
  },
  { // MV
    path: '/MV',
    component: MvPlayer
  },
  { // MV
    path: '/MobileMV',
    component: MobileMvPlayer
  },
  { // PC 菜单
    path: '/Main',
    redirect: '/Main/Pics',
    component: Main,
    children: [
      {
        path: 'Pics',
        component: PIcs
      },
      {
        path: 'Instruction',
        name: 'Instruction',
        component: Instruction
      },
      {
        path: 'MyEarth',
        name: 'MyEarth',
        component: MyEarth
      },
      {
        path: 'Map',
        name: 'Map',
        component: BTn
      },
      {
        path: 'Stones',
        name: 'Stones',
        component: Stones
      }
    ]
  },
  { // Mobile 菜单
    path: '/MobileMain',
    component: MobileMain,
    redirect: '/MobileMain/MobilePics',
    children: [
      {
        path: 'MobilePics',
        component: MobilePics
      },
      {
        path: 'MobileIntro',
        name: 'MobileIntro',
        component: MobileIntro
      },
      {
        path: 'MobileMyEarth',
        name: 'MobileMyEarth',
        component: MobileMyEarth
      },
      {
        path: 'MobileMap',
        name: 'MobileMap',
        component: MobileBtn
      },
      {
        path: 'MobileStone',
        name: 'MobileStone',
        component: MobileStone
      },
      {
        path: 'MobileRegionDetail',
        name: 'MobileRegionDetail',
        component: MobileRegionDetail
      },
      {
        path: 'detail/:id',
        name: 'MobileDetail',
        component: MobileDetail
      },
      {
        path: 'MobileSightDetail',
        name: 'MobileSightDetail',
        component: MobileSightDetail
      },
      {
        path: 'MobileMineralDetail',
        name: 'MobileMineralDetail',
        component: MobileMineralDetail
      },
      {
        path: 'MobileStoneDetail',
        name: 'MobileStoneDetail',
        component: MobileStoneDetail
      },
      {
        path: 'MobileProcessDetail',
        name: 'MobileProcessDetail',
        component: MobileProcessDetail
      },
      {
        path: 'MobileAgeDetail',
        name: 'MobileAgeDetail',
        component: MobileAgeDetail
      }
    ]
  },
  { // RegionDetail
    path: '/RegionDetail',
    component: RegionDetail,
    children: [
      {
        path: 'detail/:id',
        component: Detail
      }
    ]
  },
  { // StoneDetail
    path: '/StoneDetail',
    component: StoneDetail
  },
  { // SightDetail
    path: '/SightDetail',
    component: SightDetail
  },
  { // Mobile 登录
    path: '/MobileUser',
    name: 'MobileUser',
    component: MobileMain,
    redirect: '/MobileUser/MobileLog',
    children: [
      {
        path: 'MobileLog',
        name: 'MobileLog',
        component: MobileLog
      },
      {
        path: 'MobileReg',
        name: 'MobileReg',
        component: MobileReg
      },
      {
        path: 'MobileRepass',
        name: 'MobileRepass',
        component: MobileRepass
      },
      {
        path: 'MobileLogOut',
        name: 'MobileLogOut',
        component: MobileLogOut
      }
    ]
  },
  { // PC 登录1(swiper)
    path: '/User',
    name: 'User',
    redirect: '/User/login',
    component: Log,
    children: [
      {
        path: 'login',
        name: 'login',
        component: Sign
      },
      {
        path: 'repass',
        name: 'repass',
        component: RePass
      },
      {
        path: 'logout',
        component: SignOut
      }
    ]
  },
  { // PC 登录2(分界面)
    path: '/User2',
    name: 'User2',
    component: Log2,
    redirect: '/User2/PCLog',
    children: [
      {
        path: 'PCLog',
        name: 'PCLog',
        component: PCLog
      },
      {
        path: 'PCReg',
        name: 'PCReg',
        component: PCReg
      },
      {
        path: 'PCRePass',
        name: 'PCRePass',
        component: PCRePass
      }
    ]
  }
]

const router = new VueRouter({
  routes
})

export default router
