<template>
<div class="container">
  <h2 class="form__title">您确定要退出么？</h2>
  <div class="btn_box">
    <div class="btn" @click="sendSignOut">确定</div>
    <div class="btn" @click="remove">取消</div>
  </div>
</div>
</template>

<script>
import { getSignOut } from '@/API'

export default {
  name: 'SignOut',
  methods: {
    sendSignOut: function () {
      getSignOut({}).then((data) => {
        console.log(data)
        if (data.state !== 'success') {
          this.warn = data.messages
        } else {
          this.$store.state.loginState = false
          this.$router.push('/Main')
        }
      })
        .catch(function (err) {
          console.log(err)
        })
    },
    remove () {
      this.$router.push('/Main')
    }
  }
}
</script>

<style scoped lang="scss">
.container{
  box-shadow: 0 0 10px 0 rgba(255,255,255,0.25),
  0 0 20px 0 rgba(255,255,255,0.25);
  flex-direction: column;
  display: flex;
  align-items: center;
  justify-content: center;
  width: 500px;
  height: 420px;
  background: #CCCCCC;
  border-radius: 20px;
  box-sizing: border-box;
  margin: 0 auto;
}
.form__title {
  text-align: center;
  font-weight: 400;
  margin: 10px auto 20px auto;
  font-size: 24px;
}
.btn_box{
  display: flex;
  margin-top: 100px;
  justify-content: space-around;
}
.btn {
  border: none;
  width: 150px;
  height: 45px;
  color: #FFFFFF;
  cursor: pointer;
  font-size: 18px;
  font-weight: bold;
  text-align: center;
  letter-spacing: 10px;
  transition: transform 80ms ease-in;
  background: linear-gradient(90deg, #53524B, #429070);
  border-radius: 38px;
  padding: 10px 0;
  box-sizing: border-box;
}
.btn:nth-of-type(1){
  margin-right: 20px;
}
.btn:nth-of-type(0){
  margin-right: 20px;
}

.form > .btn {
  margin-top: 1.5rem;
}

.btn:active {
  transform: scale(0.95);
}

.btn:focus {
  outline: none;
}
</style>
