<template>
  <div class="bg">
<!--    <Sign></Sign>-->
    <!-- <div class="Intro">
        <h2 class="heading">中国地质公园（景区）自然地貌景观的前生今世</h2>
    </div> -->
    <div>
        <form action="#" class="form" id="form1">
          <div class="form_title">
            <span class="title_up">注册</span>
            <span class="title_in" @click="moveToSignIn">/登录</span>
          </div>
          <input type="email" placeholder="用户名" class="input" v-model="person.username" @focus="clear"/>
          <input type="email" placeholder="邮箱/手机号" class="input" v-model="person.email" @focus="clear"/>
          <div class="code"><input type="text" placeholder="验证码" class="input" v-model="person.code" @focus="clear"/>
            <span v-show="inCode" @click="sendCode">{{btn_text}}</span>
            <span v-show="!inCode">{{code_time}}(重新发送)</span>
          </div>
          <input type="password" placeholder="密码" class="input" v-model="person.password" maxlength="15" @focus="clear"/>
          <span class="warn">{{warn}}</span>
          <button class="btn" @click.prevent="sendSignUp">注册</button>
        </form>
    </div>
  </div>
</template>

<script>
import { postSignUp, postCode, postSignIn } from '@/API'
import qs from 'qs'
export default {
  name: 'MobileReg',
  data () {
    return {
      isActive: true,
      person: {
        username: '',
        password: '',
        email: '',
        code: ''
      },
      user: {
        username: '',
        password: ''
      },
      inCode: true,
      code_time: 0,
      btn_text: '获取验证码',
      warn: ''
    }
  },
  methods: {
    changeSignIn () {
      this.isActive = false
    },
    changeSignUp () {
      this.isActive = true
    },
    sendSignUp: function () {
      if (this.person.username === '') {
        this.warn = '请输入用户名'
      } else if (this.person.email === '') {
        this.warn = '请填写邮箱'
      } else if (this.person.password === '') {
        this.warn = '请填写密码'
      } else if (this.person.code === '') {
        this.warn = '验证码为空！'
      } else {
        postSignUp(qs.stringify(this.person)).then((data) => {
          console.log(data)
          if (data.state !== 'success') {
            this.warn = data.messages
          } else {
            this.$store.state.loginState = true
            this.$router.push('/MobileMain')
          }
        })
          .catch(function (err) {
            console.log(err)
          })
      }
    },
    sendCode: function () {
      const reg = /^([a-zA-Z]|[0-9])(\w)+@[a-zA-Z0-9]+\.([a-zA-Z]{2,4})$/
      if (reg.test(this.person.email)) {
        this.warn = ''
        postCode(qs.stringify({ email: this.person.email })).then((data) => {
          // if (data.state === 'emailError') {
          //   const msg = data.messages
          //   this.form[2].Warn = msg
          // } else {
          console.log(data)
          this.inCode = false
          this.code_time = 30
          this.timer()
        })
          .catch(function (err) {
            console.log(err)
          })
      } else {
        this.warn = '邮箱格式不正确'
      }
      console.log(this.person.email)
    },
    sendSignIn: function () {
      postSignIn(qs.stringify(this.user)).then((data) => {
        console.log(data)
        if (data.state !== 'success') {
          this.warn = data.messages
        } else {
          this.$store.state.loginState = true
          this.$router.push('/Main')
        }
      })
        .catch(function (err) {
          console.log(err)
        })
    },
    timer: function () {
      if (this.code_time > 0) {
        this.code_time--
        this.btn_text = `${this.code_time}s后重新发送`
        setTimeout(this.timer, 1000)
      } else {
        this.code_time = 0
        this.btn_text = '获取验证码'
        this.inCode = true
      }
    },
    moveToRepass () {
      this.$router.push('')
    },
    moveToSignIn () {
      this.$router.push('/MobileUser/MobileLog')
    },
    clear () {
      this.warn = ''
    }
  }
}
</script>

