  padding-top: 1.0192rem;
  // .Intro{
  //   text-align: center;
  //   .heading{
  //     font-family: 'simhei';
  //     font-size: 0.1226rem;
  //     color:#E6E6E6 ;
  //   }
  //   margin: 0 auto ;
  //   margin-bottom: 0.4033rem;
  //   overflow: hidden;
  // }
}
