    box-sizing: border-box;
    //padding: 0.1rem,0.17rem,0.0967rem,0.17rem;
    position: absolute;
    //text-align: center;
    font-size: 0.1043rem;
    color: white;
    width: 0.7833rem;
    height: 0.30rem;
    background: #4688CA;
    border-radius: 0.0533rem;
    line-height: 0.3133rem;
  }
}
