<script>
import DetailHead from '../components/DetailHead'
import vueWaterfallEasy from 'vue-waterfall-easy'
import { getState } from '@/API'
export default {
  name: 'RegionDetail',
  created () {
    this.getData()
  },
  data () {
    return {
      isClick: true,
      Title: this.$store.state.region,
      imgsArr: [], // 存放所有已加载图片的数组（即当前页面会加载的所有图片）
      fetchImgsArr: [], // 存放每次滚动时下一批要加载的图片的数组
      obj: {
        class: '安徽',
        page: 0,
        nums: 99
      }
    }
  },
  methods: {
    changeRegion (region) {
      if (region !== this.obj.class) {
        this.obj.page = 0
        this.Title = region
        this.obj.class = region
        console.log(this.obj)
        this.getData()
      }
    },
    getData () {
      getState(this.obj)
        .then(res => {
          if (this.obj.page === 1) { // 模拟已经无新数据，显示 slot="waterfall-over"
            this.$refs.waterfall.waterfallOver()
            return
          }
          this.imgsArr = res.data
          this.obj.page++
          console.log(this.imgsArr)
        })
        .catch(
          this.imgsArr = []
        )
    },
    fatherSelected (id) {
      this.$router.push({
        path: `/RegionDetail/detail/${id}`
      })
    },
    clickFn (event, { index, value }) {
      // 阻止a标签跳转
      event.preventDefault()
      // 只有当点击到图片时才进行操作
      if (event.target.tagName.toLowerCase() === 'img') {
        this.fatherSelected()
      }
    }
  },
  components: {
    DetailHead,
    vueWaterfallEasy
  }
}
</script>

