import DetailHead from '@/components/DetailHead'
import { getSight } from '@/API'
import vueWaterfallEasy from 'vue-waterfall-easy'
export default {
  name: 'StoneDetail',
  components: {
    DetailHead,
    vueWaterfallEasy
  },
  created () {
    this.count = this.stones.indexOf(this.$store.state.stoneState)
    this.getInfo()
  },
  data () {
    return {
      className: '',
      Title: this.$store.state.stoneState,
      infoList: [],
      imgsArr: [], // 存放所有已加载图片的数组（即当前页面会加载的所有图片）
      fetchImgsArr: [], // 存放每次滚动时下一批要加载的图片的数组
      stones: [
        {
          title: '矿物',
          subTitle: [
            {
              name: '硅酸盐矿物',
              list: ['矿物', '白云母', '黑云母', '辉石', '钾长石', '角闪石', '蓝闪石', '锂灰石', '锂云母', '绿帘石', '绿泥石', '绿柱石', '钠长石', '斜长石', '云母', '长石']
            },
            {
              name: '硅酸-磷酸盐矿物',
              list: ['白云石', '方解石', '石膏', '磷灰石']
            },
            {
              name: '氧化物',
              list: ['碧玺', '芙蓉石', '海蓝宝石', '尖晶石', '柯石英', '玛瑙', '石英', '重晶石', '紫水晶']
            },
            {
              name: '硫化物',
              list: ['辰砂', '方铅', '黄铁矿', '辉锑矿', '闪锌矿']
            },
            {
              name: '单矿物',
              list: ['金刚石', '硫磺']
            }
          ]
        },
        {
          title: '岩石',
          subTitle: [
            {
              name: '沉积岩',
              list: ['白云岩', '沉积岩', '鲡粒灰岩', '粉砂岩', '含砾砂岩', '火山沉积岩', '角砾岩', '砾岩', '泥岩', '泥质灰岩', '泥质砂岩', '泥质页岩', '砂砾岩', '砂岩', '砂质泥岩', '石灰岩(灰岩)', '石英砂岩', '碎屑岩', '燧石',
                '碳酸岩', '碳酸盐岩', '页岩', '油页岩', '云母砂岩', '钟乳石', '浊积岩']
            },
            {
              name: '岩浆岩',
              list: ['安山岩', '斑岩', '斑状花岗岩', '粗粒花岗岩', '粗面岩', '二长岩', '浮岩', '橄榄岩', '黑云母', '花岗岩', '花岗闪长岩', '辉绿岩', '辉长岩', '火成岩',
                '火山岩', '火山集块岩', '火山角砾岩', '火山碎屑岩', '金伯利岩', '流纹斑岩', '流纹岩', '凝灰熔岩', '凝灰岩', '熔岩', '闪长岩', '石英斑岩', '伟晶岩',
                '细晶岩', '玄武岩', '英安岩', '正长岩']
            },
            {
              name: '变质岩',
              list: ['板岩', '变质岩', '大理岩', '混合岩', '角闪岩', '蓝片岩', '榴辉岩', '绿片岩', '麻粒岩', '糜棱岩', '片麻岩', '片岩', '千枚岩', '石英岩',
                '矽卡岩', '斜长角闪岩']
            }
          ]
        },
        {
          title: '地质历史',
          subTitle: [
            {
              name: '地质年代',
              list: []
            },
            {
              name: '古生物化石',
              list: []
            },
            {
              name: '其他名称',
              list: []
            }]
        },
        {
          title: '地质作用',
          subTitle: [
            {
              name: '地壳作用',
              list: ['褶破断裂节理', '岩浆作用', '变质作用']
            },
            {
              name: '外动力作用',
              list: ['风化剥蚀', '水动力作用', '冰川作用', '重力作用']
            }]
        }
      ]
    }
  },
  methods: {
    getInfo: function (value) {
      if (value !== this.className) {
        this.className = value
        getSight({ class: this.className }).then((data) => {
          this.infoList = data.data
          console.log(data)
          this.imgsArr = data.data
        })
          .catch(function (err) {
            console.log(err)
          })
      }
    },
    changeTitle (title) {
      this.Title = title
    }
  }
}
</script>

