<template>
<div class="mainInfo">
<DetailHead></DetailHead>
  <div class="show">
    <div class="titleBox">
    <p>
      <span></span>
      {{ title }}
    </p>
    </div>
    <div class="informationBox">
      <div class="leftBox">
        <div class="leftBoxIn">
        <mavon-editor
          class="md"
          :value="content"
          :subfield="prop.subfield"
          :defaultOpen="prop.defaultOpen"
          :toolbarsFlag="prop.toolbarsFlag"
          :editable="prop.editable"
          :scrollStyle="prop.scrollStyle"
          :toolbars="markdownOption"
        />
        </div>
      </div>
      <div class="right">
        <div class="tab"  @click="changeMarginUp">
          <span class="aT"></span>
        </div>
        <div class="picBox">
          <ul :style="{marginTop: move*-435 +'px'}">
            <li v-for="(value, index) in imgData" :key="index"><img :src="value.image" alt=""></li>
          </ul>
        </div>
        <div class="tab"  @click="changeMarginDown">
          <div class="aB"></div>
        </div>
      </div>
    </div>
  </div>
</div>
</template>

<script>
import DetailHead from '@/components/DetailHead'
import { getInfo } from '@/API'
import { mavonEditor } from 'mavon-editor'
import 'mavon-editor/dist/css/index.css'

window.onload = function () {
  const s1 = document.getElementById('div_03')
  console.log(s1)
  s1.onclick = show
  function show () {
    console.log(1)
    const s2 = document.getElementById('div_04')
    if (s2.classList.contains('show')) s2.classList.remove('show')
    else s2.classList.add('show')
  }
}
export default {
  name: 'Detail',
  components: {
    DetailHead,
    mavonEditor
  },
  data () {
    return {
      title: '',
      content: '',
      imgData: [],
      move: 0,
      markdownOption: {
        fontSize: '20px',
        lineHeight: '30px',
        toolbars: {
          imagelink: true
        }
      }
    }
  },
  created () {
    getInfo({ id: this.$route.params.id })
      .then(res => {
        console.log(res)
        this.title = res.data.title
        this.content = res.data.content
        this.imgData = res.data.pictures
        console.log(this.imgData)
      })
  },
  methods: {
    changeMarginDown () {
      if (this.move < 5) { this.move++ }
    },
    changeMarginUp () {
      if (this.move > 0) { this.move-- }
    }
  },
  computed: {
    // 解析器配置
    prop () {
      const res = {
        subfield: false, // 单双栏模式
        defaultOpen: 'preview', // edit： 默认展示编辑区域 ， preview： 默认展示预览区域
        editable: false, // 是否允许编辑
        toolbarsFlag: false,
        scrollStyle: true
      }
      return res
    }
  }
}
</script>

