import Vue from 'vue'
import Vuex from 'vuex'
Vue.use(Vuex)

export default new Vuex.Store({
  state: {
    user: {
      username: 'not set',
      password: '',
      email: 'email not set',
      phone: 'phone not set'
    },
    menuState: false,
    stoneState: '岩石',
    loginState: false,
    region: '北京',
    sight: '丹霞风貌'
  },
  mutations: {
    resetStore (state) {
      Object.keys(state).forEach(key => {
        // state[key] = deepClone(window.SITE_CONFIG['storeState'][key])
        state[key] = JSON.parse(JSON.stringify(window.SITE_CONFIG.storeState[key]))
      })
    }
  },
  actions: {
  },
  modules: {
  }
})
