<template>
<div class="page">
    <div class="herder">
        <div class="TitleBox">
          <i class="el-icon-sort"  @click="showPicker" v-show="leftShow"></i>
          <i class="null"  v-show="!leftShow"></i>
          <h2>{{Title}}</h2>
          <i class="el-icon-check"  @click="showPicker" v-show="!leftShow"></i>
          <i class="null"  v-show="leftShow"></i>
        </div>
    </div>
    <div class="page-picker" v-show="ifclick">
      <div class="page-picker-wrapper">
        <mt-picker :slots="addressSlots" @change="onAddressChange" :visible-item-count="3"></mt-picker>
      </div>
  </div>
  <div class="picShow">
    <vue-waterfall-easy :imgsArr="imgsArr"
                          @scrollReachBottom="getData">
      <div class="img-info" slot-scope="props" @click="fatherSelected(props.value.id)">
        <p class="some-info infoTitle">{{props.value.title}}</p>
      </div>
      <div slot="waterfall-over" >已经到底啦</div>
    </vue-waterfall-easy>
  </div>
</div>
</template>

<script>
/* eslint-disable */
const address = ['地质年代', '古生物化石', '其它名词']
import vueWaterfallEasy from 'vue-waterfall-easy'
import { getNoun } from '@/API'
export default {
  name: 'MobileAgeDetail',
  created () {
    this.getData()
  },
  data () {
    return {
      leftShow: true,
      rightShow: true,
      Title: '地质年代',
      ifclick: false,
      addressSlots: [
        {
          flex: 1.8,
          values: address,
          className: 'slot1',
          textAlign: 'center'
        }
      ],
      address1: '',
      imgsArr: [], // 存放所有已加载图片的数组（即当前页面会加载的所有图片）
      fetchImgsArr: [], // 存放每次滚动时下一批要加载的图片的数组
      obj: {
        class: '地质年代',
        page: 1,
        nums: 8
      }
    }
  },
  methods: {
    showPicker () {
      if(this.ifclick){
        this.Title = this.address1;
        if (this.address1 !== this.obj.class) {
        this.obj.class = this.address1
        this.obj.page = 1
        this.imgsArr = []
        this.getData()
      }
      }
      this.ifclick = !this.ifclick
      this.leftShow = !this.leftShow
    },
    onAddressChange(picker, values) {
        picker.setSlotValues(1, address[values[0]]);
        this.address1 = values[0];
        // this.Title = this.address1;
      },
    getData () {
      getNoun(this.obj)
        .then(res => {
          console.log(res)
          if (this.obj.page === res.max_page + 1) { // 模拟已经无新数据，显示 slot="waterfall-over"
            this.$refs.waterfall.waterfallOver()
            return
          }
          this.imgsArr = this.imgsArr.concat(res.data)
          this.obj.page++
        })
        .catch(
          // console.log('error happen')
        )
    },
    fatherSelected (id) {
      this.$router.push({
        path: `/MobileMain/detail/${id}`
      })
    },
    clickFn (event, { index, value }) {
      // 阻止a标签跳转
      event.preventDefault()
      // 只有当点击到图片时才进行操作
      if (event.target.tagName.toLowerCase() === 'img') {
        this.fatherSelected()
      }
    }
  },
  components: {
     vueWaterfallEasy
  }
}
</script>
<style scoped lang="scss">
.picShow{
  width: 3.6rem;
  height: 5.4rem;
  margin-top: 0.1rem;
}
.infoTitle{
  padding: 0.12rem 0.05rem;
  text-align: center;
  background-color: whitesmoke;
  box-sizing: border-box;
  border-radius: 0.03rem;
  font-size: 0.18rem;
  line-height: 0.18rem;
}
.el-icon-sort{
  color:#429070;
  font-size: 0.3rem;
  font-weight: bolder;
}
.null{
  width: 0.3rem;
}

.el-icon-check{
  color:#429070;
  font-size: 0.3rem;
  font-weight: bolder;
}

.page{
  width: vw;
  background: #E4E4E4;
  // box-shadow: 0px 1px 32px 0px rgba(96, 96, 96, 0.25);
}
.page-picker{
  position: absolute;
  left:0.5rem;
  z-index: 1;
  width: 2.6rem;
  margin-left:auto;
  margin-right: auto;
}
.page-picker-wrapper{
  padding-top:0.1rem;
  padding-bottom:0.1rem;
  font-size: 0.2rem;
  background-color: rgba($color: #fff, $alpha: 0.97);
  border-radius: 0.1rem;
}
.herder{
  margin: 5px 13.33px;
  padding: 0.1333rem auto;
  box-sizing: border-box;
  border-bottom: 1px solid  #A7A7A7;
}
.TitleBox{
  margin: 0 auto;
  display: flex;
  width: 2.2rem;
  justify-content: space-between;
  align-items: center;
  height: 0.7333rem;
  span{
    display: inline-block;
    width: 0.25rem;
    height: 0.03rem;
    background: #429070;
  }
  h2{
    font-weight: 800;
    //width: 160px;
    font-size: 0.22rem;
    text-align: center;
  }
}
</style>
<style lang="scss">
.page {
.picker-selected{
  font-weight: bolder;
  font-size: 0.2rem;
}
}
</style>