<template>
  <div>
      <div class="logobox">
        <img src="../../assets/img/logoUd.png" class='logo'>
      </div>
      <form action="#" class="form" id="form2">
        <div class="mask">
        </div>
        <div class="form_title">
          <span href="#" class="title_in">登录</span>
          <span href="#" class="title_up" @click="moveToSignUp">/注册</span>
        </div>
        <input type="text" placeholder="邮箱/手机号" class="input" v-model="user.username" @focus="clear"/>
        <input type="password" placeholder="密码" class="input" v-model="user.password" @focus="clear"/>
        <span href="#" class="link" @click="moveToRepass">忘记密码了?</span>
        <span class="warn">{{warn}}</span>
        <button class="btn" @click.prevent="sendSignIn"><span class="btntext">登录</span></button>
      </form>
  </div>
</template>

<script>
import { postSignUp, postCode, postSignIn } from '../../API/index'
import qs from 'qs'
export default {
  name: 'MobileLog',
  data () {
    return {
      isActive: true,
      person: {
        username: '',
        password: '',
        email: '',
        code: ''
      },
      user: {
        username: '',
        password: ''
      },
      inCode: true,
      code_time: 0,
      btn_text: '获取验证码',
      warn: ''
    }
  },
  methods: {
    changeSignIn () {
      this.isActive = false
    },
    changeSignUp () {
      this.isActive = true
    },
    sendSignUp: function () {
      postSignUp(qs.stringify(this.person)).then((data) => {
        if (data.state !== 'success') {
          this.warn = data.messages
        } else {
          this.$store.state.loginState = true
          this.$router.push('/Main')
        }
      })
        .catch(function (err) {
          console.log(err)
        })
    },
    sendCode: function () {
      const reg = /^([a-zA-Z]|[0-9])(\w)+@[a-zA-Z0-9]+\.([a-zA-Z]{2,4})$/
      if (reg.test(this.person.email)) {
        this.warn = ''
        postCode(qs.stringify({ email: this.person.email })).then((data) => {
          // if (data.state === 'emailError') {
          //   const msg = data.messages
          //   this.form[2].Warn = msg
          // } else {
          console.log(data)
          this.inCode = false
          this.code_time = 30
          this.timer()
        })
          .catch(function (err) {
            console.log(err)
          })
      } else {
        this.warn = '邮箱格式不正确'
      }
      console.log(this.person.email)
    },
    sendSignIn: function () {
      postSignIn(qs.stringify(this.user)).then((data) => {
        if (data.state !== 'success') {
          this.warn = data.messages
        } else {
          this.$store.state.loginState = true
          this.$router.push('/Main')
        }
      })
        .catch(function (err) {
          console.log(err)
        })
    },
    timer: function () {
      if (this.code_time > 0) {
        this.code_time--
        this.btn_text = `${this.code_time}s后重新发送`
        setTimeout(this.timer, 1000)
      } else {
        this.code_time = 0
        this.btn_text = '获取验证码'
        this.inCode = true
      }
    },
    moveToRepass () {
      this.$router.push('/User2/PCRepass')
    },
    moveToSignUp () {
      this.$router.push('/User2/PCReg')
    },
    clear () {
      this.warn = ''
    }
  }
}
</script>

<style scoped lang="scss">
.logobox {
  text-align: center;
  .logo{
    height: 1.18rem;
    width: 1.22rem;
  }
  margin-bottom: 0.21rem;
}

.form{
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  height: 100%;
  text-align: center;
  margin-left: 7.03rem;
  margin-right: 7.03rem;
  border-radius: 0.2rem;
  padding-top:0.4rem;
  padding-bottom:0.34rem;
  background-color: rgba($color:#ffffff, $alpha: 0.5);
}
.form_title {
  font-family: 'jiangxizhuokai';
  display: inline-block;
  margin-bottom: 1.50rem;
  .title_in{
    color: #000000;
    font-size: 0.48rem;
    margin-right: 0.1rem;
    cursor: pointer;
  }
  .title_up{
    color:#666666;
    font-size: 0.38rem;
    cursor: pointer;
  }
}

.input {
  font-size: 0.22rem;
  box-sizing: border-box;
  background-color: rgba($color: #ffffff, $alpha: 0.85);
  border: none;
  padding-left: 0.28rem;
  border-radius: 0.1rem;
  margin-bottom: 0.27rem;
  width: 3.5rem;
  height: 0.6rem;
  outline: none;
  font-weight: bolder;
}

.link {
  font-family: 'jiangxizhuokai';
  margin-top: 0.5966rem;
  margin-bottom: 0.2933rem;
  color: #000000;
  font-size: 0.22rem;
  font-weight: 500;
  text-decoration: none;
  cursor: pointer;
}
.btn {
  background-color: #FFCA29;
  font-family: 'jiangxizhuokai';
  border: none;
  width: 2.15rem;
  height: 0.75rem;
  cursor: pointer;
  font-size: 0.3rem;
  text-align: center;
  border-radius: 0.38rem;
  color: #1A1A1A;
}
// placeholder属性设置
::-webkit-input-placeholder { color:#4D4D4D; font-family: 'jiangxizhuokai'; font-weight: initial; font-size: 0.18rem;}
::-moz-placeholder { color:#4D4D4D; font-family: 'jiangxizhuokai'; font-weight: initial; font-size: 0.18rem;} /* firefox 19+ */
:-ms-input-placeholder { color:#4D4D4D; font-family: 'jiangxizhuokai'; font-weight: initial; font-size: 0.18rem;} /* ie */
input:-moz-placeholder { color:#4D4D4D; font-family: 'jiangxizhuokai'; font-weight: initial; font-size: 0.18rem;}
</style>
