<template>
    <div id="china_map" style='width:800px;height:600px;'  ref="ChinaMap"> </div>
</template>

<script>
import 'echarts/map/js/china'
const echarts = require('echarts')
export default {
  name: 'Map',
  mounted () {
    const _this = this
    this.$nextTick(() => {
      _this.chinachart = echarts.init(document.getElementById('china_map'))
      _this.chinachart.on('click', function (param) {
        console.log(param)
        _this.$store.state.region = param.name
        console.log(_this.$store.state.region)
        _this.$router.push('/RegionDetail')
      })
      // 初始化echarts实例
      // 进行相关配置
      _this.chartOption = {
        geo: { // 地理坐标系组件用于地图的绘制
          map: 'china', // 表示中国地图
          // roam: true, // 是否开启鼠标缩放和平移漫游
          zoom: 1.2, // 当前视角的缩放比例（地图的放大比例）
          label: {
            show: true
          },
          itemStyle: { // 地图区域的多边形 图形样式。
            borderColor: 'rgba(0, 0, 0, 0.2)',
            emphasis: { // 高亮状态下的多边形和标签样式
              areaColor: 'green',
              shadowBlur: 20,
              shadowColor: 'rgba(0, 0, 0, 0.5)'
            }
          }
        }
      }
      // 使用刚指定的配置项和数据显示地图数据
      _this.chinachart.setOption(this.chartOption)
    })
  },
  methods: {
    show () {
      this.chinachart.on('click', function (params) {
        console.log(params)
      })
    }
  },
  data () {
    return {
      value1: true,
      value2: true
    }
  }

}
</script>

<style scoped lang="scss">
#china_map{
  margin: 40px auto;
}

</style>
