<template>
<div class="page">
    <div class="herder">
        <div class="TitleBox">
          <span></span>
          <h2>{{Title}}</h2>
          <span></span>
        </div>
    </div>
    <div class="picShow">
      <vue-waterfall-easy :imgsArr="imgsArr"
                              @scrollReachBottom="getData">
          <div class="img-info" slot-scope="props" @click="fatherSelected(props.value.id)">
            <p class="some-info infoTitle">{{props.value.title}}</p>
          </div>
          <div slot="waterfall-over" >已经到底啦</div>
      </vue-waterfall-easy>
    </div>
</div>
</template>

<script>
/* eslint-disable */
// const address = {
//     '沉积岩':  ['白云岩', '沉积岩', '鲡粒灰岩', '粉砂岩', '含砾砂岩', '火山沉积岩', '角砾岩', '砾岩', '泥岩', '泥质灰岩', '泥质砂岩', '泥质页岩', '砂砾岩', '砂岩', '砂质泥岩', '石灰岩(灰岩)', '石英砂岩', '碎屑岩', '燧石',
//                 '碳酸岩', '碳酸盐岩', '页岩', '油页岩', '云母砂岩', '钟乳石', '浊积岩'],
//     '岩浆岩':  ['安山岩', '斑岩', '斑状花岗岩', '粗粒花岗岩', '粗面岩', '二长岩', '浮岩', '橄榄岩', '黑云母', '花岗岩', '花岗闪长岩', '辉绿岩', '辉长岩', '火成岩',
//                 '火山岩', '火山集块岩', '火山角砾岩', '火山碎屑岩', '金伯利岩', '流纹斑岩', '流纹岩', '凝灰熔岩', '凝灰岩', '熔岩', '闪长岩', '石英斑岩', '伟晶岩',
//                 '细晶岩', '玄武岩', '英安岩', '正长岩'],
//     '变质岩':  ['板岩', '变质岩', '大理岩', '混合岩', '角闪岩', '蓝片岩', '榴辉岩', '绿片岩', '麻粒岩', '糜棱岩', '片麻岩', '片岩', '千枚岩', '石英岩',
//                 '矽卡岩', '斜长角闪岩']
// }
import vueWaterfallEasy from 'vue-waterfall-easy'
import { getNoun } from '@/API'
export default {
  name: 'MobileStoneDetail',
  created () {
    this.getData()
  },
  data () {
    return {
      Title:'岩石',
      imgsArr: [], // 存放所有已加载图片的数组（即当前页面会加载的所有图片）
      obj: {
        class: '岩石',
        page: 1,
        nums: 7
      }
    }
  },
  methods: {
    getData () {
      getNoun(this.obj)
        .then(res => {
           console.log(res)
          if (this.obj.page === res.max_page + 1) { // 模拟已经无新数据，显示 slot="waterfall-over"
            this.$refs.waterfall.waterfallOver()
            return
          }
          this.imgsArr = this.imgsArr.concat(res.data)
          this.obj.page++
        })
        .catch(
          
        )
    },
    fatherSelected (id) {
      this.$router.push({
        path: `/MobileMain/detail/${id}`
      })
    },
    clickFn (event, { index, value }) {
      // 阻止a标签跳转
      event.preventDefault()
      // 只有当点击到图片时才进行操作
      if (event.target.tagName.toLowerCase() === 'img') {
        this.fatherSelected()
      }
    }
  },
  components: {
    vueWaterfallEasy
  }
}
</script>
<style scoped lang="scss">
.picShow{
  width: 3.6rem;
  height: 5.4rem;
  margin-top: 0.1rem;
}
.infoTitle{
  padding: 0.12rem 0.05rem;
  text-align: center;
  background-color: whitesmoke;
  box-sizing: border-box;
  border-radius: 0.03rem;
  font-size: 0.18rem;
  line-height: 0.13rem;
}
.page{
  width: vw;
  background: #E4E4E4;
}
.herder{
  padding: 0.1333rem auto;
  box-sizing: border-box;
  border-bottom: 1px solid  #A7A7A7;
}
.TitleBox{
  margin: 0 auto;
  display: flex;
  width: 1.2334rem;
  justify-content: space-between;
  align-items: center;
  height: 0.7333rem;
  span{
    display: inline-block;
    width: 0.19rem;
    height: 0.0267rem;
    background: #429070;
  }
  h2{
    font-weight: 800;
    font-size: 0.30rem;
    text-align: center;
  }
}
</style>
